.services {
    .delivery-slider {
        .card {
            align-items: flex-start;
            height: 482px;
            &:before {
                z-index: 1;
                content: "";
                display: inline-block;
                top: 0;
                right: 0;
                bottom: 0;
                left: 0;
                position: absolute;
                background: linear-gradient(0deg, #040511e8 0%, #58585800 100%);
            }
        }
        .card-image {
          

            img {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
            }
        }
       
        .card-title {
            margin-bottom: 12px;
            line-height: 124.5%;
            text-align: center;
            margin-top: auto;
        }

        .card-descr {
            margin-bottom: 14px;;
        }

        .card-title,
        .card-descr,
        .card-button-row {
            z-index: 1;
        }
    }
}